.charts-info {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  border-bottom: 1px solid rgb(143, 143, 143);
  justify-content: center;
}

.charts {
  flex-wrap: wrap;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.title {
  font-family: "Montserrat", sans-serif;
  padding-top: 10px;
  display: flex;
  font-size: 30pt;
  flex-direction: column;
  align-items: center;
}
